.App {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}


#map {
  width: 100%;
  height: 100%;
  max-width: 1020px;
  max-height: 575px;
  /* border: 1px solid red; */
  margin: 10px;
}

.leaflet-marker-icon {
  background: none;
  border: none;
}

/* ZONES */

.markerPopup-zone {
  height: 100%;
  opacity: 1;
}

.markerPopup-zone > span {
  /* visibility: hidden; */
  background: linear-gradient(to left, rgba(10, 10, 10, 0.5) 90%, rgba(10, 10, 10, 0.0));
  border-top: 1px solid #aaa;
  border-bottom: 1px solid #aaa;
  color: white;
  position: absolute;
  width: max-content;
  right: 26px;
  top: 4px;
  padding: 2px 8px;
  /* border-radius: 4px 0 0 4px; */
}

.markerPopup-zone:hover > span {
  visibility: visible;
}

.markerPopup-zone > img {
  height: 32px;
  width: 32px;
  position: absolute;
}

/* TEMPLES */

.markerPopup-temple {
  height: 100%;
}

.markerPopup-temple > span {
  /* display: none; */
  /* background: linear-gradient(to left, rgba(123, 255, 0, 0.5) 90%, rgba(10, 10, 10, 0.0)); */
  border-top: 1px solid #aaa;
  border-bottom: 1px solid #aaa;
  color: white;
  position: absolute;
  width: max-content;
  right: 26px;
  top: 4px;
  padding: 2px 8px;
  /* border-radius: 4px 0 0 4px; */
}

.markerPopup-temple > img {
  height: 32px;
  width: 32px;
  position: absolute;
}

.red {
  background: linear-gradient(to left, rgba(255, 0, 0, 0.5) 90%, rgba(10, 10, 10, 0.0));
}
.green {
  background: linear-gradient(to left, rgba(123, 255, 0, 0.5) 90%, rgba(10, 10, 10, 0.0));
}
.blue {
  background: linear-gradient(to left, rgba(0, 110, 255, 0.5) 90%, rgba(10, 10, 10, 0.0));
}
.yellow {
  background: linear-gradient(to left, rgba(255, 251, 0, 0.5) 90%, rgba(10, 10, 10, 0.0));
}
.purple {
  background: linear-gradient(to left, rgba(183, 0, 255, 0.5) 90%, rgba(10, 10, 10, 0.0));
}
.orange {
  background: linear-gradient(to left, rgba(255, 102, 0, 0.5) 90%, rgba(10, 10, 10, 0.0));
}

header {
  background: rgb(13, 18, 22);
  color: #fff;
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
}

nav > a {
  padding: 5px 10px;
  border-right: 1px solid #555;
}

nav > a:last-child {
  border: none;
}

nav > a:hover {
  color: #ddd;
}

h1 {
  text-align: center;
}

.App > span {
  display: flex;
  flex-direction: column;
  align-items: center;
}

footer {
  font-size: 0.8rem;
}
